	module.exports = {"data":{"markdownRemark":{"html":"<p>Someone brought a piece of black and white paper, saying it was a three-dimensional painting. Just stare at it and you will see a three-dimensional picture. I watched it for a long time and didn't see it. After seeing it out, I realized that when I started to look at it, my mind was too clear. This kind of picture had to blur the line of sight, and then I had a picture, and it was three-dimensional, like transparent glass. It’s amazing. I don't know who invented it, and the inventor is really not easy.</p>\n<p>People tend to be confused. The more concentrated they are, the more confused they are, sometimes they are sloppy, but they will not be confused.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0086.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0086.md","frontmatter":{"title":"May 5th Thursday"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0086.md absPath of file >>> MarkdownRemark"}}

